<template>
  <div>
    <el-dialog :title="update==true?'菜单添加':'菜单修改'" :visible.sync="dialogFormVisible" @close="close"
    >
      
      <el-form :model="form">
        
        
        <el-form-item
          label="规格名称"
          :label-width="formLabelWidth"
        >
          <el-input v-model="form.specsname" autocomplete="off"></el-input>
        </el-form-item>


        <el-form-item
          label="规格属性"
          :label-width="formLabelWidth" v-for="(item,index) in arr" :key="index"
        >
        <div class="box1">
          <el-input autocomplete="off" v-model="item.value"></el-input>
          <el-button type="primary" @click="add" v-if="index==0">新增规格属性</el-button>
          <el-button type="danger" @click="del(index)" v-else>删除</el-button>
        </div>
        </el-form-item>

        

        <el-form-item label="状态"  :label-width="formLabelWidth">
            
          <el-switch
            v-model="form.status"
            :active-value="1"
            :inactive-value="2"
            active-color="#13ce66"
            inactive-color="#ff4949">
          </el-switch>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle">取 消</el-button>
        <el-button type="primary" @click="adduserList" v-if="update">添加</el-button>
        <el-button type="primary" @click="updateuser" v-else>修改</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import {alertSuccess,alertWarn} from '../../../utils/alert'
import { specsadd ,specsedit,specsinfo} from "../../../utils/request";
export default {
  props: ['update'],
  components: {},
  data() {
    return {
      arr:[{value:''}],
      dialogFormVisible: false,
      form: {
        specsname: "",
        attrs: "",
        status: 0,
      },
      formLabelWidth: "120px",
    };
  },
  computed: {
    ...mapGetters({
      roleList:"roleList"
    }),
  },
  methods: {
    ...mapActions({
        getspecsList:"getspecsList",
        getuserList:"getuserList",
        specsTotal1:"specsTotal1"
    }),
    add(){
      this.arr.push({value:''})
    },
    del(index){
      this.arr.splice(index,1)
    },
    close(){
      this.dialogFormVisible=false;
        this.clearForm()
        this.arr=[{value:''}]
    },
    cancle(){
        this.dialogFormVisible=false;
        this.clearForm()
        this.arr=[{value:''}]
    },
    clearForm(){
      this.form={
        specsname: "",
        attrs: "",
        status: 0,
      }
    },
    adduserList() {
      this.dialogFormVisible = false;
      this.form.attrs = JSON.stringify(this.arr.map(item=>item.value))
      specsadd(this.form).then(res=>{
        this.getspecsList()
        this.clearForm()
        this.arr=[{value:''}]
        this.specsTotal1()
      })
    },
    getspecsDetail(id){
      specsinfo(id).then(res=>{
        this.form=res.data.list[0]
        this.form.id=id
        this.arr=JSON.parse(this.form.attrs).map(item=>({value:item}))
      })
    },
    updateuser(){
      specsedit(this.form).then(res=>{
        if(res.data.code==200){
                alertSuccess(res.data.msg)
                this.dialogFormVisible = false;
                this.clearForm()
                this.arr=[{value:''}]
                this.getspcesList()
              }else{
                alertWarn(res.data.msg)
              }
      })
    },
    
  },
  mounted() {
    this.specsTotal1()
  },
};
</script>
<style scoped>
.box1{
  display: flex;
}
.box1 .add{
  width: 120px;
  flex: 1;
}
.box1 .del{
  width: 80px;
  flex: 1;
}
</style>


