<template>
  <div>
    <el-table
      :data="specsList"
      style="width: 100%; margin-bottom: 20px"
      row-key="id"
      border
      :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
    >
      <el-table-column prop="id" label="规格编号" sortable width="180">
      </el-table-column>
      <el-table-column prop="specsname" label="规格名称" sortable width="180">
      </el-table-column>
      <el-table-column prop="attrs" label="规格属性">
        
          <template slot-scope="scope">
            <el-tag v-for="(item,index) in JSON.parse(scope.row.attrs)" :key="index">{{item}}</el-tag>
          </template>
        
         </el-table-column>
      <el-table-column prop="status" label="状态">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1"
            >启用</el-button
          >
          <el-button type="primary" v-if="scope.row.status == 2"
            >禁用</el-button
          >
        </template>
      </el-table-column>
      <el-table-column prop="icon" label="操作">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)"
            >修改</el-button
          >
          <el-button type="danger" @click="del(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background layout="prev, pager, next" :total="specstotal" :page-size="specssize"
    @current-change="CurrentChange"></el-pagination>
    
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import { specsdel } from "../../../utils/request";
import { alertWarn, alertSuccess } from "../../../utils/alert";
export default {
  props: [],
  components: {},
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      specsList: "specsList",
      specstotal:"specstotal",
      specssize:"specssize",
    }),
  },
  methods: {
    ...mapActions({
      specsTotal1:"specsTotal1",
      reqchangespecspage:"reqchangespecspage",
      getspecsList:"getspecsList"
    }),
    edit(id) {
      this.$emit("update", id);
    },
    del(id) {
      this.$confirm("是否确认删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          specsdel(id).then((res) => {
            if (res.data.code == 200) {
              alertSuccess(res.data.msg);
              this.specsTotal1()
              this.getspecsList();
            } else {
              alertWarn(res.data.msg);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    CurrentChange(e){
      this.reqchangespecspage(e)
    }
  },
  mounted() {
    this.getspecsList();
  },
};
</script>
<style scoped>
</style>


