<template>
<div>
    <el-button type="primary" @click="isShow" class="btn">添加</el-button>
    <v-list @update="update"></v-list>
    <v-add ref="vchild" :update="isadd"></v-add>
</div>
</template>
<script>
 import {mapGetters,mapActions} from 'vuex';
 import vList from './component/list'
 import vAdd from './component/add'
export default {
props: [],
components: {vList,vAdd},
data() {
return {
    isadd:false
};
},
computed: {
...mapGetters({}),
},
methods: {
...mapActions({}),
isShow(){
    this.isadd=true
    this.$refs.vchild.dialogFormVisible = true
},
update(id){
    this.isadd=false
    this.$refs.vchild.dialogFormVisible = true
    this.$refs.vchild.getspecsDetail(id)
}
},
mounted(){},
};
</script>
<style scoped>
.btn{
    margin-top: 20px;
}
</style>


